.footer {
  display: flex;
  justify-content: space-between;
  padding: 38px 40px 38px 40px;
  border-top: 1px solid #3f50e6;
}

.footer-right {
  display: flex;
  gap: 40px;
}

.footer-right-section h1 {
  margin-top: 0px;
  font-weight: 400;
  font-size: 28px;
}

.footer-right-section p {
  font-weight: 300;
  font-size: 20px;
  line-height: 40px;
}
